<template>
  <table class="table table-bordered table-striped table-hover">
    <thead>
      <slot name="thead"/>
    </thead>
    <tbody>
      <slot name="tbody"/>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'DataTable',
  mounted() {
    $(this.$el).DataTable({
      columnDefs: [{
        targets: 'no-sort',
        orderable: false
      }],
      order: [],
      language: {
        processing: '...',
        loadingRecords: '...',
        emptyTable: '',
        info: ' _START_ - _END_ _TOTAL_ ',
        zeroRecords: '',
        search: '_INPUT_',
        infoFiltered: '( _MAX_ )',
        searchPlaceholder: '...',
        lengthMenu: '  _MENU_  ',
        infoEmpty: ' 0 ',
        paginate: {
          first: '',
          last: '',
          next: '',
          previous: ''
        }
      }
    })
  }
}
</script>
