<template>
  <div>
    <div
      v-for="(item, index) in list"
      :key="item.key"
    >

      <div class="form-group row">
        <label class="col-lg-1 col-form-label">
           {{ index + 1 }}
        </label>

        <div class="col-lg-5">
          <autosizeTextarea
            v-model="item.main_content"
            :placeholder="textareaPlaceholder"
            rows="1"
            class="form-control"
          />
        </div>

        <div class="w-100 py-1 d-lg-none" />

        <div
          :class="clockPickerCol"
          class="col-lg-2"
        >
          <clockPicker
            v-model="item.time"
            placeholder=""
            @blur.native="afterTimePick"
            @afterDone="afterTimePick"
          />
        </div>

        <div class="col-2 pl-0 text-center col-lg-1 pl-lg-1 text-lg-left">
          <TrashIcon @click.native="removeItem(item.key)"/>
        </div>
      </div>
    </div>

    <div
      v-if="!isDataEmpty"
      class="text-center">
      <PlusIcon @click.native="addItem"/>
    </div>

    <div
      v-if="isDataEmpty"
      class="text-center">
      <button
        type="button"
        class="btn btn-outline-info"
        @click="addItem"></button>
    </div>

    <input
      v-model="listJson"
      :id="idd"
      :name="idd"
      hidden
      type="text"
    >
  </div>

</template>

<script>

export default {
  name: 'AddableList',
  props: {
    listProp: {
      type: null,
      default() {
        return []
      }
    },
    textareaPlaceholder: {
      type: String,
      default: ''
    },
    idd: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      list: []
    }
  },
  computed: {
    listJson: function() {
      return JSON.stringify(this.list)
    },
    listSize: function() {
      return this.list.length - 1
    },
    showDeleteIcon: function() {
      return this.list.length !== 1
    },
    clockPickerCol: function() {
      return this.list.length === 1 ? 'col-12' : 'col-10'
    },
    isDataEmpty: function() {
      return this.list.length === 0
    }
  },
  watch: {
    listProp: function(newVal, oldVal) {
      if (newVal) {
        if (typeof newVal === 'string') {
          this.list = JSON.parse(newVal)
        } else {
          this.list = newVal
        }
      } else {
        this.list = []
      }
    }
  },
  methods: {
    addItem() {
      this.list.push({
        main_content: '',
        time: '',
        key: new Date().getTime().toString()
      })
    },
    removeItem(key) {
      const index = this.list
        .map(item => item.key)
        .indexOf(key)

      this.list.splice(index, 1)
    },
    afterTimePick() {
      this.list.sort(function(current, next) {
        if (current.item > next.tiem) {
          return 1
        } else if (current.time < next.time) {
          return -1
        }
        return 0
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .btn-outline-info:hover {
    color: white;
  }
</style>
